___CSS_LOADER_EXPORT___.push([module.id, `* {
	box-sizing: border-box;
}

*:focus-visible {
	outline: 0.5px rgba(150, 150, 150, 0.23) solid;
	box-shadow: inset 0px 0px 20px 0px #8080803b;
	border-radius: 3px;
}

html {
	font-family: Helvetica;
	font-size: 16px;
	height: 100vh;
	background: #e6e6e6;
}

p {
	margin: 0;
}

body {
	position: relative;
	height: 100vh;

	display: grid;
	grid-template-rows: 1fr 10fr;
}

header {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.mainTitle {
	text-align: center;
}

/* Add TODO BTN */
.cancelForm:hover,
.deleteTodo:hover,
.deleteTodoEdit:hover,
.cancelListForm:hover {
	background: #ff5d5d;
	color: #700;
}

.cancelForm,
.deleteTodo,
.deleteTodoEdit,
.cancelListForm {
	height: 0.9rem;
	width: 0.9rem;
	padding: 0;

	border: 1px solid #808080b8;
	border-radius: 100%;
	background: #b4b4b436;
	backdrop-filter: blur(15px);

	font-size: 0.6rem;
	text-align: center;
	color: transparent;
}

.addTodoBtn {
	z-index: 10;
	position: fixed;
	left: 12rem;
	top: 1.7rem;

	height: 40px;
	width: 40px;
	margin: 0;
	padding: 0;
	padding-bottom: 0.5px;

	border: solid 1px gray;
	border-radius: 30px;
	background: #b4b4b436;
	backdrop-filter: blur(15px);
	box-shadow: 5px 5px 12px 1px #80808057;

	font-size: 1.5rem;
	cursor: pointer;
}

.hamburger {
	z-index: 10;
	position: absolute;
	left: 6rem;
	top: 2rem;

	padding: 5px 10px;
	border-radius: 10px;
	border: solid 1px gray;
	background: #b4b4b436;
	backdrop-filter: blur(15px);
	box-shadow: 5px 5px 12px 1px #80808057;

	cursor: pointer;
}

.sidebar {
	z-index: 10;
	display: flex;
	flex-direction: column;
	gap: 30px;

	width: 200px;

	position: fixed;
	transition: transform 0.3s;
	transform: translateX(-150%);

	top: 6rem;
	left: 4rem;
	right: 0;
	bottom: 0;
}

.showSidebar {
	transform: translateX(0);
}

.defaultLists,
.customLists {
	display: flex;
	flex-direction: column;
	gap: 10px;

	background: #b4b4b436;
	backdrop-filter: blur(15px);

	border: 1px solid gray;
	border-radius: 20px;
	box-shadow: 5px 5px 12px 1px #80808057;

	padding: 10px 30px;
	margin: 0px 10px;

	color: gray;
}

.defaultLists > .sidebarButton:last-child,
.customLists > .sidebarButton:last-child {
	border: 0;
}

.sidebarButton {
	padding: 5px 2px;

	border: 0;
	border-bottom: solid #80808040 1px;
	background: transparent;

	text-align: left;
	font-size: 0.9rem;
	letter-spacing: 0.3px;
	text-overflow: ellipsis;

	cursor: pointer;
}

.container {
	position: relative;
}

#listForm {
	width: 100%;
	position: relative;
	display: grid;
	grid-template-rows: 1fr 3fr;
	gap: 5px;
	border-radius: 10px;
}

.cancelListForm {
	position: absolute;
	top: 5px;
	left: -1.2rem;
}

#listTitle {
	width: 90%;
	background: transparent;
	border: 0;
	border-bottom: 1px solid #93939391;
	padding: 5px;
	font-size: 1rem;
}

#listDescription {
	width: 90%;
	background: transparent;
	border: 0;
	font-size: 0.8rem;
	padding: 5px;
}

.listSubmitButton {
	position: absolute;
	bottom: -3.5rem;
	left: 1.5rem;

	height: 2rem;
	width: 4.1rem;
	border: solid 1px gray;
	border-radius: 20px;
	background: #b4b4b436;
	backdrop-filter: blur(15px);
	box-shadow: 5px 5px 12px 1px #80808057;

	font-size: 1rem;
}

/** LIST */

.list {
	display: flex;
	flex-direction: column;
	height: 100%;
	width: 40%;
	margin: auto;
}

.listTitle {
	display: flex;
	align-items: center;

	height: 40px;
	margin: 0 1.2rem;

	font-size: 1.5rem;
	font-weight: bold;
	text-transform: uppercase;
	letter-spacing: 0.1rem;
	text-overflow: ellipsis;
}

.listDescription {
	display: flex;
	align-items: end;
	height: 20px;
	margin: 0 1.2rem;
	font-size: 0.8rem;
	font-weight: lighter;
	color: gray;
	font-style: italic;
	text-overflow: ellipsis;
}

.customTitle,
.customDescription {
	background: transparent;
	border: 0;
}

.completedTodosTitle {
	display: flex;
	align-items: center;

	max-width: 550px;
	margin: 0px 1rem;
	padding: 0.2rem 0;
	border-bottom: 1px solid #80808061;

	font-size: 0.9rem;
	color: #00000059;
	letter-spacing: 0.5px;
	text-overflow: ellipsis;
}

.activeTodos,
.completedTodos {
	display: flex;
	flex-direction: column;

	gap: 5px;

	padding: 15px 0px;
	padding-top: 10px;

	overflow: scroll;
}

/* TODO */

/* TODO FORM */

#todoForm {
	z-index: 1;
	position: absolute;
	top: 30%;
	left: 50%;
	transform: translate(-50%, -50%);

	display: grid;
	grid-template-rows: repeat(4, 2.5rem);
	grid-template-columns: 1fr 1fr 1fr;

	width: 400px;
	margin: auto;
	padding: 15px 13px;

	border: 1px solid gray;
	border-bottom: 5px solid #9f9b9bd1;
	border-radius: 20px;
	background: #b4b4b436;
	backdrop-filter: blur(15px);
	box-shadow: 5px 5px 12px 1px #80808057;
	text-overflow: ellipsis;
}

.cancelForm {
	position: absolute;
	top: 5px;
	left: 10px;
}

#formTitle,
#formNotes,
#formDate,
#formList,
#formPriority {
	height: 100%;
	width: 100%;
	padding: 5px 5px;

	border: 0;
	border-bottom: 1px solid #a1a2a430;

	background: transparent;
}

.formTitle {
	grid-area: 1 / 1 / 1 / 4;
	align-self: center;
}

#formTitle {
	width: 95%;
	height: 60%;
	padding: 5px 10px;
	text-overflow: ellipsis;
}

.formNotes {
	grid-area: 2 / 1 / 4 / 4;
	align-self: center;

	height: 100%;
	width: 100%;
	margin-bottom: 3px;

	border-radius: 10px;
	border: 1px solid #a1a2a430;
}

#formNotes {
	padding: 5px 10px;
	border: 0;
}

textarea {
	resize: none;
	overflow: scroll;
}

.formDate,
.formList,
.formPriority {
	align-self: end;
	justify-self: center;
}

.formDate {
	grid-area: 4 / 1;
}

#formDate {
	width: 90%;
}

.formList {
	grid-area: 4 / 2;
}

#formList {
	width: 100%;
	padding: 5px;
}

.formPriority {
	grid-area: 4 / 3;
}

#priority {
	width: 100%;
}

.placeholderPri {
	color: gray;
}

.submitButton {
	position: absolute;
	bottom: -2.5rem;

	height: 1.8rem;
	width: 6.5rem;

	border: 1px solid #808080b8;
	border-radius: 10px;
	background: #b4b4b436;
	backdrop-filter: blur(15px);
	box-shadow: 5px 5px 12px 1px #80808057;

	align-self: end;
	justify-self: center;
}

/* TODO CARD */

.todoCard {
	position: relative;

	display: flex;
	column-gap: 10px;

	width: 90%;
	max-width: 550px;
	margin: 0.5rem 0.5rem 0rem;
	padding: 10px 5px;

	border-radius: 20px;
	border: 1px solid gray;
	border-bottom: 5px solid #8080809c;

	background: #b4b4b436;
	backdrop-filter: blur(15px);
	box-shadow: 3px 5px 12px 0px #00000042;

	cursor: pointer;
}

.todoCheck {
	margin-left: 10px;
}

.todoDate {
	font-size: 0.7rem;
	color: gray;
	align-self: end;
}

.deleteTodo {
	position: absolute;
	top: 30%;
	right: 10px;
}

.todoCheck {
	/* margin-left: 10px; */
	grid-area: 1 / 1 / 3 / 2;
	justify-self: center;
}

/* TODO CARD - edit */

.todoCardEdit {
	position: relative;

	display: grid;
	grid-template-columns: 0.1fr 0.5fr 0.5fr 0.5fr;
	grid-template-rows: 1fr 1fr 1fr 1fr;

	width: 90%;
	padding: 10px 5px;
	margin: 0.5rem 0.5rem 0rem;

	border-radius: 20px;
	border: 1px solid gray;
	border-bottom: 5px solid #8080809c;
	background: #b4b4b436;
	backdrop-filter: blur(15px);
	box-shadow: 3px 5px 12px 0px #00000042;
}

.deleteTodoEdit {
	position: absolute;
	right: 10px;
	top: 10px;
}

.todoTitleEdit,
.todoDueDateEdit,
.todoNotesEdit,
.todoPriorityEdit {
	background: transparent;
	border: 0;
	text-overflow: ellipsis;
}

.todoNotesEdit,
.todoDueDateEdit,
.todoPriorityEdit {
	font-size: 0.8rem;
}

.todoTitle {
	max-height: 1.1rem;
	overflow: auto;
	max-width: 75%;
}

.todoCheckEdit {
	grid-row: 1 / span 4;
	align-self: flex-start;
	margin-top: 10%;
	border-radius: 10px;
}

.todoTitleEdit {
	grid-column: 2 / span 3;
}

.todoNotesEdit {
	grid-column: 2 / span 3;
	grid-row: 2 / span 2;
	width: 95%;
}

.todoDueDateEdit {
	grid-column: 2;
	grid-row: 4;

	justify-self: start;
	width: 70%;
}

.todoListEdit {
	grid-column: 3;
	grid-row: 4;
	justify-self: start;

	border: 0;
	background: transparent;

	font-size: 0.9rem;
}

.todoPriorityEdit {
	grid-column: 4;
	grid-row: 4;
	align-self: end;
	justify-self: start;

	width: 85%;
}

.done > .todoCard > * {
	color: #8080807d;
	text-decoration: line-through;
	color: rgba(128, 128, 128, 0.64);
}

/* Priority */
.low {
	border: 1px solid lightblue;
	border-bottom: 5px solid lightblue;
	box-shadow: 3px 5px 12px 0px #92bcca99;
}

.medium {
	border: 1px solid orange;
	border-bottom: 5px solid orange;
	box-shadow: 3px 5px 12px 0px #bd7a005e;
}

.high {
	border: 1px solid #ff5b5b;
	border-bottom: 5px solid #ff5b5b;
	box-shadow: 3px 5px 12px 0px #db020259;
}
